
.two{
    background: rgba(255,255,255,1);
    background: -moz-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(138,191,255,1) 100%);
    background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, rgba(255,255,255,1)), color-stop(100%, rgba(138,191,255,1)));
    background: -webkit-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(138,191,255,1) 100%);
    background: -o-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(138,191,255,1) 100%);
    background: -ms-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(138,191,255,1) 100%);
    background: radial-gradient(ellipse at center, rgba(255,255,255,1) 0%, rgba(138,191,255,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#8abfff', GradientType=1 );
    text-align: center;
    overflow: auto;
    position: relative;

  }
  .experiences{
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: auto;
  }
 .exp-title{
     display: flex;
     font-size: 18px;
     color: black;
     flex-direction: row;
     justify-content: center;
     margin-bottom: 10px;
 } 
.exp-ul{
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    margin-top: -1px;
    flex-wrap: wrap;
    justify-content: center;
}
.exp-ul li {
    list-style: none;
}
.exp-ul .exp-detail div {
    padding: 5px;
}
.what{
    font-size: 1.1em;
}
.where{
        font-size: 1.2857em; /* 18/16 -> 18px */
        font-weight: 300;
        display: inline;
        margin-right: 0.5em;
}
.when{
        font-size: 0.75em; /* 12/16 -> 12px */
        font-weight: 700;
        text-transform: uppercase;
        color: #fff;
        background-color: #444444;
        display: inline-block;
        padding: 0 12px;
        margin: -5px 0.5em 0 0 !important;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
}

.exp-ul li {
    position:relative; /* so that pseudoelements are positioned relatively to their "li"s*/
    /* use padding-bottom instead of margin-bottom.*/ 
    margin-bottom: 0; /* This overrides previously specified margin-bottom */
    padding-bottom: 2.5em;
}


.exp-ul li:after {
    /* bullets */
    content: '\25CF';
    font-size: 38px;
    color:cadetblue;
    position: absolute;
    left: -28px;
    top: -17px;
}

.exp-ul li:before {
    /* lines */
    content:"";
    position: absolute;
    left: -19px;
    top: 12px;
    border-width: 3px;
    border-style: solid;
    border-image: 
      linear-gradient(
        to bottom, 
        #383a3a, 
        rgba(0, 0, 0, 0)
      ) 1 100%;
    height: 100%;
}

.exp-ul li:first-child:before {
   /* first li's line */
   top: 6px; /* moves the line down so that it disappears under the bullet. Adjust manually */
}

.exp-ul li:last-child:before {
    /* last li's line */
   height: 6px; /* shorten the line so it goes only up to the bullet. Is equal to first-child:before's top */
}
@media(max-width: 768px){
    .exp-title{
        font-size: 14px;
    }
    .where{
        font-size: 1em;
    }
    .what{
        font-size: .9em;
    }
    .when{
        font-size: 0.55em;
    }
    .exp-ul li:after {
        font-size: 28px;
    }
}
@media(max-width: 480px){
    .exp-title{
        font-size: 12px;
    }
    .where{
        font-size: 0.8em;
    }
    .what{
        font-size: .7em;
    }
    .when{
        font-size: 0.45em;
    }
    .exp-ul li:after {
        font-size: 18px;
    }
}
@media(max-width: 321px){
    .exp-title{
        font-size: 10px;
    }
    .where{
        font-size: 0.6em;
    }
    .what{
        font-size: .5em;
    }
    .when{
        font-size: 0.35em;
    }
    .exp-ul li:after {
        font-size: 12px;
    }
}

